import React from 'react';

const TripBooking = ({ setSelectedTrip }) => {
  const trips = [
    { id: 1, name: 'Moon Base Alpha' },
    { id: 2, name: 'Mars Space Station' },
    { id: 3, name: 'Lunar Hotel' }
  ];

  return (
    <div>
      <h2>Select Your Trip</h2>
      <ul>
        {trips.map((trip) => (
          <li key={trip.id}>
            <button onClick={() => setSelectedTrip(trip)}>
              {trip.name}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default TripBooking;
