import React from 'react';

const Accommodation = ({ selectedTrip }) => {
  const accommodationOptions = {
    'Moon Base Alpha': ['Orbiting Hotels', 'Lunar Surface Cabins'],
    'Mars Space Station': ['Mars Orbit Suites', 'Red Planet Motel'],
    'Lunar Hotel': ['Lunar Suites', 'Lunar Cottages']
  };

  return (
    <div>
      <h2>Accommodation Options for {selectedTrip.name}</h2>
      <ul>
        {accommodationOptions[selectedTrip.name].map((option, index) => (
          <li key={index}>{option}</li>
        ))}
      </ul>
    </div>
  );
};

export default Accommodation;
