import React from 'react';

const Pricing = ({ selectedTrip }) => {
  const pricingOptions = {
    'Moon Base Alpha': [
      { type: 'Luxury Cabin', price: '$5000' },
      { type: 'Economy Shuttle', price: '$2000' }
    ],
    'Mars Space Station': [
      { type: 'VIP Zero-Gravity', price: '$10000' },
      { type: 'Economy Shuttle', price: '$3000' }
    ],
    'Lunar Hotel': [
      { type: 'Suite', price: '$7000' },
      { type: 'Standard Room', price: '$3500' }
    ]
  };

  return (
    <div>
      <h2>Pricing Options for {selectedTrip.name}</h2>
      <ul>
        {pricingOptions[selectedTrip.name].map((option, index) => (
          <li key={index}>
            {option.type}: {option.price}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Pricing;
