import React, { useState } from 'react';
import TripBooking from './components/TripBooking';
import Pricing from './components/Pricing';
import Accommodation from './components/Accommodation';
import Dashboard from './components/Dashboard';
import AIChatbot from './components/AIChatbot';
import SolarSystem from './components/SolarSystem';
import './App.css';

function App() {
  const [selectedTrip, setSelectedTrip] = useState(null);

  return (
    <div className="App">
      <SolarSystem />
      <div className="content">
        <h1>Welcome to the Space Travel Booking Platform</h1>
        <p>Your gateway to booking the best space trips and accommodation.</p>

        <TripBooking setSelectedTrip={setSelectedTrip} />
        {selectedTrip && (
          <>
            <Pricing selectedTrip={selectedTrip} />
            <Accommodation selectedTrip={selectedTrip} />
            <Dashboard />
          </>
        )}

        <AIChatbot />
      </div>
    </div>
  );
}

export default App;
